import React, { Suspense } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux'
import store from './Components/Redux/Store'
import CustomErrorBoundaryForRoutes from './Components/Common/CustomErrorBoundaryForRoutes';


const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(

  <Provider store={store}>
    <CustomErrorBoundaryForRoutes errorMsg="Something went wrong !!" >
      <BrowserRouter basename='/citizen'>
        <Suspense fallback={<div className="font-bold text-lg italic h-screen w-screen flex items-center justify-center">Loading...</div>}>
          <App />
        </Suspense>
      </BrowserRouter>
    </CustomErrorBoundaryForRoutes>
  </Provider>
  // document.getElementById('root')


);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
